.history {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(100%);
    background-color: #fff;
    padding: 16px;
    transition: transform 1s ease;
    border: 5px solid #3767ad;

    &--visible {

        transform: translateX(0);
    }

    &__wrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        background-color: #fff;
        height: 100%;
        max-width: 700px;
        margin: 0 auto;


    }

    .table {
        table-layout: fixed;
        border-collapse: collapse;
        border: 5px solid #3767ad;
        background-color: #fff;
        width: 80%;
        box-shadow: 0px 1px 7px 1px black;


        &__header {
            font-size: 20px;
            background: #3767ad;
            color: #fff;
            line-height: 40px;
        }

        &__body {
            letter-spacing: 1px;
        }

        &__day {
            border: 5px solid #3767ad;
            line-height: 40px;
            text-align: center;
            font-weight: bold;
        }
    }

    @media (orientation: landscape) {
        .table {
            width: 60%;
        }
    }

    .button {
        margin: 0 auto;
        height: 24px;
        width: 184px;
        font-size: 14px;
        line-height: 2px;
        margin-top: 36px;
        outline: none;
        color: #fff;
        background: #3767ad;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transition: transform 0.5s ease;

        &:hover {
            transform: translateY(-8%);
        }
    }
}